import { ErrorHandler } from './ErrorHandler';
import { retryErrorSrc176Evid103 } from '@wix/error-handler-core/@wix/bi-logger-os-data/v2';
import { ErrorAccessor } from '../state/ErrorAccessor';
import { withoutDefaults } from '../util/withoutDefaults';

export interface ReportErrorRetryBiParams {
  readonly errorAccessor: ErrorAccessor;
}

export function reportErrorRetryBi(
  errorHandler: ErrorHandler,
  params: ReportErrorRetryBiParams,
) {
  const { biLogger } = errorHandler;
  const { errorAccessor } = params;
  const { errorHandlerState } = errorAccessor;

  const sessionId = errorHandlerState?.sessionId;

  if (!sessionId) {
    return;
  }

  biLogger.report(
    withoutDefaults(retryErrorSrc176Evid103)({
      session_id: sessionId,
    }),
  );
}
